import React from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';

// icons for buttons
import Octocat from '../img/octocat.png';
import resumeIcon from '../img/resume-icon.png';

// assets
import Resume from '../assets/Nicholas Baker Resume.pdf';

class Navbar extends React.Component {
  constructor(props){
    super(props);
    this.state = {  scrollPos: 0  }
  }
  componentDidMount() {
    window.onscroll = () => {
      this.setState({ scrollPos: window.pageYOffset });
    };
  }
  componentWillUnmount() {
    window.onscroll = null;
  }
  render() {
    return (
      <div>
        <header className="navbar">
          <a className="name" href="./">Nick Baker</a>
          <div >
            <div className="link-nav">
              <a className="info-link" href="https://github.com/ndbaker1" target="_blank" rel="noopener noreferrer"><img src={Octocat} alt="github" width="40px"/></a>
              <a className="info-link" href={Resume} target="_blank" rel="noopener noreferrer"><img src={resumeIcon} alt="resume" width="40px"/></a>
            </div>
            <div className="right-nav link-nav" >
              <AnchorLink href="#projects">Projects</AnchorLink>
              <AnchorLink href="#contact">Contact</AnchorLink>
            </div>
          </div>
        </header>
        <AnchorLink className={"top-link "+(this.state.scrollPos > 100 ? "off-screen" : "")} href="#about"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 6"><path d="M12 6H0l6-6z"/></svg></AnchorLink>
      </div>
    )
  }
}

export default Navbar;